.popup-edit-avatar__container {
  padding-top: 34px;
  width: 430px;
  min-height: 272px;
  background: #ffffff;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 528px) {
  .popup-edit-avatar__container {
    width: 282px;
    min-height: 322px;
  }
}
